.dashboard-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  font-family: 'Inter', sans-serif;
  background-color: #f9fafb;
  color: #333;
}

.dashboard-title {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: #222;
  text-align: center;
}

.summary-cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.summary-cards div {
  flex: 1;
  min-width: 200px;
  padding: 1rem 1.5rem;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  font-size: 1.1rem;
  font-weight: 500;
  transition: transform 0.2s ease;
}

.summary-cards div:hover {
  transform: translateY(-3px);
}

.chart-section,
.transactions-section {
  background-color: #ffffff;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.chart-section h2,
.transactions-section h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #222;
}

.transactions-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

.transactions-table th,
.transactions-table td {
  padding: 12px 10px;
  border-bottom: 1px solid #e0e0e0;
}

.transactions-table th {
  background-color: #f1f5f9;
  font-weight: 600;
  color: #444;
  text-align: left;
}

.transactions-table tbody tr:hover {
  background-color: #f9f9f9;
  transition: background-color 0.2s;
}

.loading {
  font-size: 1.3rem;
  text-align: center;
  margin-top: 3rem;
}
